.wrapper { max-width: 4000px; padding: 0; overflow: hidden; margin: 0 auto; position: relative; font-family: 'Inter',  Arial,  sans-serif; font-style: normal; font-weight: 500; font-size: 16px; line-height: 160%; color: #91929B; background:  #F1F4F9; }

a { color: #03B25D;}
p { font-family: 'Inter',  Arial,  sans-serif; font-weight: 500; font-style: normal; font-size: 16px; line-height: 160%; color:  #91929B; padding: 0; margin: 0 0 1.2em 0; }

h2{
   font-family: 'Inter',  Arial,  sans-serif; 
   font-weight: 600; 
   font-size: 18px; 
   line-height: 120%; 
   color: #343643;
   padding: 0; 
   margin: 0 0 30px 0; 
  }
@media (min-width: 1230px) { .container { width: 100%; } }
@media (min-width: 1500px) { .container { width: 1470px; } }
h3{
    font-weight: 900;
    font-size: 20px;
    line-height: 120%;
    margin: 0 0 40px 0;
    color: #343643;
}
strong{
  color: #343643;
}
.header_top { 
  width: 100%; 
  position: relative; 
  margin-bottom: 0px; 
  z-index: 10; 
  padding: 0;
}
.header_top-container{
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: nowrap;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}
.link_top { 
  font-weight: 500; 
  font-size: 14px; 
  line-height: 120%; 
  color: #03B25D;
  text-decoration: none; 
  margin-left: 30px; 
  display: inline-block; 
  vertical-align: middle; 
}
.link_top span,
.link_top img{
  display: inline-block; 
  vertical-align: middle; 
}
.link_top img{
  margin-right: 3px;
}
.banner_section{
  background-position: center center; 
  background-size: cover; 
  background-repeat: no-repeat; 
  padding: 130px 0 130px 0;
  position: relative;
}
.banner_title { 
  font-weight: 900;
  font-size: 60px;
  line-height: 120%;
  color: #343643;
  margin: 0 0 30px 0;
}
.banner_title span { display: block; }
.banner_text{
  font-weight: 600;
  font-size: 18px;
  line-height: 120%;
  color: #91929B;
  margin: 0 0 30px 0;
}

.btn_main {
  cursor: pointer;
  background: #35B275;
  border-radius: 10px;
  font-weight: bold;
  font-size: 14px;
  line-height: 50px;
  height: 50px;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  display: inline-block;
  padding: 0 50px;
  min-width: 220px;
  max-width: 100%;
  color: #fff;
  letter-spacing: 0.04em;
  transition: all 0.2s ease;
}
.btn_main:hover {
    background: #298a5a;
  }
.title_section{
  font-weight: 900;
  font-size: 38px;
  line-height: 120%;
  color: #343643;
  margin: 0 0 50px 0;
}




.footer { background:  #404751; padding: 0; position: relative;}
.footer_top { padding: 30px 0 30px 0; border-bottom: 1px solid rgba(255, 255, 255, 0.1); }
.logo-foot{
  margin-bottom: 20px;
  text-decoration: none;
  display: inline-block;
}
.logo-foot img{
  display: block;
}
.adress_block{
  margin: 0 0 30px 0;
}
.adress_element { font-weight: 500; font-size: 16px; line-height: 160%; color: #FFFFFF; margin: 0 0 10px 0; }
.adress_element span { display: block; }
.row_foot{
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: nowrap;
  -ms-flex-wrap: nowrap;
  flex-wrap: nowrap;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-align-content: stretch;
  -ms-flex-line-pack: stretch;
  align-content: stretch;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}
.footer_soc-container{
  text-align: right;
}
.foot_social a { display: inline-block; vertical-align: middle; margin: 0 34px 0 0; }
.foot_social img { width: 40px; height: 40px; }
.foot_social a:hover { text-decoration: none; opacity: 0.8; }
.footer_bottom { padding: 30px 0 30px 0; }
.copy { font-weight: 500; font-size: 16px; line-height: 160%; color:  #9FA3A8; }
.footer_list-block {  text-align: right; }
.footer_list { list-style: none; margin: 0; padding: 0; }
.footer_list li { display: inline-block; vertical-align: top; margin: 0  50px 0 0; }
.footer_list li:last-child { margin: 0  0px 0 0; }
.footer_list li a { font-weight: 500; font-size: 16px; line-height: 160%; color: #C6C8CB; text-decoration: none; margin: 0; padding: 0; }
.link_foot-list
.link_top{
  color: #9FA3A8;
  margin: 0 40px 0 0 ;
}
.footer_list-block{
  float: right;
}
.main{}
.section{
  padding: 50px 0 50px 0;
}
.section_index{
  padding: 70px 0 50px 0;
}
.title_top{
  font-weight: 400;
  font-size: 24px;
  line-height: 120%;
  color: #35B275;
  margin: 0 0px 10px 0 ;
}
.text_block{
  margin: 0 0px 40px 0 ;
}

.s_bread{
  padding:  30px 0 0 0;
}
.bredcrumb {
  display: block;
  list-style: none;
  margin: 0;
  padding: 0;
}
.bredcrumb a, 
.bredcrumb span {
  text-decoration: none;
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 160%;
  color: #6D6F7A;
  display: inline-block;
}
.bredcrumb .sep {
  color: #03B25D;
  margin:  0 4px 0 3px ;
}
.bredcrumb
.breadcrumb_last{
  color: #999BA1;
}
.section_page{
  padding:  150px 0 0 0;
}
.title_page{
  font-weight: 900;
  font-size: 60px;
  line-height: 120%;
  color: #343643;
  margin: 0 0 50px 0;
}
.title_h2{
  font-weight: 600;
  font-size: 18px;
  line-height: 120%;
  color: #343643;
  margin: 50px 0 35px 0;
}
.title_h3{
  font-weight: 600;
  font-size: 16px;
  line-height: 120%;
  color: #343643;
  margin: 20px 0 15px 0;
}
.adress-title {
  font-weight: 600;
  font-size: 18px;
  line-height: 120%;
  color: #343643;
  margin: 20px 0 30px 0;
}
.adress_block {
  margin: 0 0 20px 0;
}
.adress_block-img {
  float: left;
  margin-right: 10px;
}
.adress_block-img img {
  width: 24px;
  height: 24px;
}
.adress_block-text{
  overflow: hidden;
}
.adress_block-text a, 
.adress_block-text {
  display: block;
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
  color: #6D6F7A;
  text-decoration: none;
}

.adress_block-text strong {
    margin: 0 0 5px 0;
    display: block;
}















.sticky-block { display: block;  background: #FFFFFF;
 box-shadow: 0px 2px 12px rgba(0, 0, 0, 0.2);  padding: 20px 0; }
.sticky-block.fixed { position: fixed; top: 0; left: 0; right: 0; z-index: 1000; padding: 5px 0;  opacity: 0; transition: all 0.2s ease; }
.sticky-block.animate { opacity: 1; }


.result_block{
  background: #FFFFFF;
  box-shadow: 0px 8px 24px rgba(52, 54, 67, 0.2);
  border-radius: 30px;
  margin: 15px auto;
}
.result_block-head{
  border-bottom:  1px solid #C2C3C7;
  padding: 30px 30px  10px  30px;
}
.result_block-title{
  font-weight: 900;
  font-size: 24px;
  line-height: 120%;
  color: #343643;
  margin: 0 0 15px 0;
}
.result_block-check{
  font-weight: 600;
  font-size: 18px;
  line-height: 120%;
  color: #03B25D;
  min-height: 30px;
  margin: 0 0 10px 0;
  padding: 5px 0 5px 38px;
  background-size: auto;
  background-position: left 0;
  background-repeat: no-repeat;
}
.check-ok{
  color: #03B25D;
  background-image: url(../img/ok.svg);
}
.check-notok{
  color: #E53247;
  background-image: url(../img/notok.svg);
}
.result_block-body{
  padding: 10px 30px 10px 30px;
}
.result_line{
  margin: 20px 0 20px 0;
}
.result_line:after,
.result_line:before{
  content: '';
  display: block;
  width: 100%;
  clear: both;
}
.result_name{
  width: 20%;
  padding-right: 30px;
  font-weight: 500;
  font-size: 14px;
  line-height: 120%;
  color: #999BA1;
  float: left;
}
.result_text{
  font-weight: 500;
  font-size: 16px;
  line-height: 160%;
  color: #343643;
  width: 80%;
  float: left;
}
.result_text img{
  width: auto;
  max-width: 100%;
  margin: 10px 0 0 0;
}
@media screen and (max-width: 1750px) { }
@media screen and (max-width: 1400px) { }
@media screen and (min-width: 1200px) and (max-width: 1400px){ }
@media screen and (max-width: 1200px) {
  .result_name {
    width: 30%;
  }
  .result_text {
    width: 70%;
  }
}
@media screen and (max-width: 991px) { 
  .link_foot-list
  .link_top{
    margin: 5px 20px 5px 0;
  }
  .title_page {
    font-size: 50px;
    margin: 0 0 30px 0;
  }
  .section_page {
      padding: 80px 0 0 0;
  }
  .banner_title {
    font-size: 46px;
  }
  .banner_section {
    padding: 70px 0 70px 0;
  }
  .title_top {
    font-size: 18px;
    margin: 0 0px 10px 0;
  }
  .title_section {
    font-size: 30px;
    margin: 0 0 30px 0;
  }
  .section_index {
    padding: 50px 0 40px 0;
  }
  .result_block-title {
    font-size: 20px;
  }
}
@media screen and (max-width: 767px) { 
  .row_foot{
    display: block;
  }
  .footer_adress-block{
    width: 100%;                      
    display: block;
  }
  .footer_soc-container{
    text-align: center;
  }
  .footer_top {
    padding: 20px 0 30px 0;
  }
  .foot_social{
    margin-top: 20px;
  }
  .footer_list-block,
  .footer_copy-block{
    width: 100%;
    float: none;
    text-align: left;
  }
  .footer_copy-block{
    margin-top: 10px;
  }
  .footer_bottom {
    padding: 10px 0 20px 0;
  }
  .footer_list li,
  .footer_list li:last-child {
    display: block;
    margin: 10px 0 10px 0;
  }
  .title_page {
    font-size: 36px;
    margin: 0 0 30px 0;
  }
  .section_page {
    padding: 50px 0 0 0;
  }
  .header_right{
    text-align: right;
  }

  .link_top {
    margin-left: 0px;
    display: block;
    text-align: left;
  }
  .sticky-block {
    padding: 10px 0;
  }
  .logo img,
  .logo-foot img{
    max-width: 70px;
  }
  .banner_title {
    font-size: 36px;
    white-space: pre-line;
    word-wrap:  break-word;
    margin: 0 0 20px 0;
  }
  .banner_title  span{
    display: inline;
  }
  .banner_section {
    padding: 0px 0 0px 0;
  }
  .banner_text-container{
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-flex-wrap: nowrap;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -webkit-justify-content: space-between;
    -ms-flex-pack: justify;
    justify-content: space-between;
    min-height: 560px;
    padding-top: 50px;
    padding-bottom:  30px;
  }
  .btn_main {
    width: 100%;
    padding: 0 10px;
  }
  .title_top {
    font-size: 14px;
    margin: 0 0px 10px 0;
  }
  .title_section {
    font-size: 20px;
    margin: 0 0 20px 0;
  }
  .section_index {
    padding: 40px 0 40px 0;
  }
  .result_name {
    width: 100%;
  }
  .result_text {
    width: 100%;
  }
  .result_block-head {
    padding: 20px 15px 0px 15px;
  }
  .result_block-body {
    padding: 0px 15px 10px 15px;
  }
  .result_line {
    margin: 30px 0 30px 0;
  }

}
@media screen and (max-width: 500px) { }